<template>
  <div class="hc-goods">
    <mt-navbar class="hc-goods-header" v-model="selected">
      <mt-tab-item id="1">商品介绍</mt-tab-item>
      <mt-tab-item id="2">规格参数</mt-tab-item>
    </mt-navbar>
    <mt-tab-container v-model="selected">
      <!-- 商品介绍 -->
      <mt-tab-container-item id="1">
        <!-- banner -->
        <div class="hc-goods-banner">
          <mt-swipe class="hc-swiper" :auto="4000">
            <mt-swipe-item v-for="(item, index) in product.imgUrlList" :key="index">
              <div class="hc-banner-item">
                <img :src="item.url" alt="">
              </div>
            </mt-swipe-item>
          </mt-swipe>
        </div>
        <!-- 介绍 -->
        <div class="hc-goods-intr">
          <div class="hc-intr-title">{{product.productName}}</div>
          <div class="hc-intr-intr hc-f14">{{product.subtitle}}</div>
          <div class="hc-intr-reco hc-f14">推荐指数：<Star :score="recommendPoint" slot="right"/></div>
          <!-- 价格 -->
          <div class="hc-goods-cost hc-pr">
            <span>￥ </span><span class="hc-f16 hc-cost-cost">{{product.price}}</span>
            <!-- <div class="hc-cost-cart hc-pa">
              <transition name="hc-roll">
                <div v-show="tempNum>=1" class="hc-cart-icon" @click="subtractFromCart">
                  <svg class="icon hc-subtract" aria-hidden="true">
                    <use xlink:href="#icon-ziyuan2"></use>
                  </svg>
                </div>
              </transition>
              <div v-show="tempNum>=1" class="hc-cart-icon hc-f16 hc-cart-num">{{tempNum}}</div>
              <div class="hc-cart-icon" @click="addToCart">
                <svg class="icon hc-add" aria-hidden="true">
                  <use xlink:href="#icon-ziyuan3"></use>
                </svg>
              </div>
            </div> -->
            <hc-cart-btn
              :tempNum="tempNum"
              @subtractFromCart="subtractFromCart"
              @addToCart="addToCart"
            ></hc-cart-btn>
          </div>
        </div>
        <!-- 配送店铺 -->
        <div class="hc-goods-shop">
          <div class="hc-shop-bar hc-pr">配送店铺<div class="hc-bar-icon hc-pa"></div></div>
          <div class="hc-shop-msg hc-pr">
            <div class="hc-shop-img hc-pa">
              <img :src="store.imgUrl" alt="">
            </div>
            <div class="hc-shop-intr">
              <div class="hc-f16">{{store.storeName}}</div>
              <div class="hc-f12">{{store.adress}}</div>
            </div>
          </div>
        </div>
        <!-- 图片展示 -->
        <div class="hc-goods-display">
          <!-- {{product.detail}} -->
          <div v-html="product.detail"></div>
        </div>
        <!-- 产品参数 -->
        <div class="hc-goods-params">
          <div class="hc-params-item">
            <div class="hc-item-left">产品编号：</div>
            <div class="hc-item-right">{{product.spec.productId}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">规格：</div>
            <div class="hc-item-right">{{product.spec.nums}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">产品重量：</div>
            <div class="hc-item-right">{{product.spec.weight}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">包装方式：</div>
            <div class="hc-item-right">{{product.spec.boxType}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">主体原料：</div>
            <div class="hc-item-right">{{product.spec.material}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">储存方法：</div>
            <div class="hc-item-right">{{product.spec.storgeMethod}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">原麦汁浓度：</div>
            <div class="hc-item-right">{{product.spec.initPercent}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">酒精度：</div>
            <div class="hc-item-right">{{product.spec.winePercent}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">国产/进口：</div>
            <div class="hc-item-right">{{product.spec.source}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">保质期：</div>
            <div class="hc-item-right">{{product.spec.exp}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">原产地：</div>
            <div class="hc-item-right">{{product.spec.sourceArea}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">类别：</div>
            <div class="hc-item-right">{{category.name}}</div>
          </div>


        </div>

      </mt-tab-container-item>
      <mt-tab-container-item id="2">
        <!-- 产品参数 -->
        <div class="hc-goods-params">
          <div class="hc-params-item">
            <div class="hc-item-left">产品编号：</div>
            <div class="hc-item-right">{{product.spec.productId}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">规格：</div>
            <div class="hc-item-right">{{product.spec.nums}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">产品重量：</div>
            <div class="hc-item-right">{{product.spec.weight}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">包装方式：</div>
            <div class="hc-item-right">{{product.spec.boxType}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">主体原料：</div>
            <div class="hc-item-right">{{product.spec.material}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">储存方法：</div>
            <div class="hc-item-right">{{product.spec.storgeMethod}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">原麦汁浓度：</div>
            <div class="hc-item-right">{{product.spec.initPercent}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">酒精度：</div>
            <div class="hc-item-right">{{product.spec.winePercent}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">国产/进口：</div>
            <div class="hc-item-right">{{product.spec.source}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">保质期：</div>
            <div class="hc-item-right">{{product.spec.exp}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">原产地：</div>
            <div class="hc-item-right">{{product.spec.sourceArea}}</div>
          </div>
          <div class="hc-params-item">
            <div class="hc-item-left">类别：</div>
            <div class="hc-item-right">{{category.name}}</div>
          </div>
        </div>
      </mt-tab-container-item>
    </mt-tab-container>
    <hc-shop-cart
    v-model="popupRoleVisible"
    ref="hcCart"
    :userId='userId'
    :storeId='storeId'
    :productId='product.spec.productId'
    ></hc-shop-cart>
  </div>
</template>

<script>
import Star from '../../components/common/star/Star'
import hcShopCart from '../../components/common/hcShopCart'
import HcCartBtn from '../../components/common/hcCartBtn/hcCartBtn'
import { getProductDetail } from '../../api/axios.js'
export default {
  components: {
    Star,
    hcShopCart,
    HcCartBtn
  },
  data() {
    return {
      selected: '1',
      // 返回的数据
      // 商品分类对象
      category: {},
      // 商品对象
      product: {spec:{}},
      // 推荐指数， 5为满星
      recommendPoint: 0,
      // 门店对象
      store: {},
      cartShow: false,
      // 加入购物车
      tempNum: 0,
      popupRoleVisible: false,
      // 购物车需要的参数
      userId: '',
      storeId: '',
    }
  },
  methods: {
    // 交互操作
    addToCart() {
      // 如果tempNum为0那么是第一次增加
      this.tempNum += 1;
      this.$refs.hcCart.updataCart({
        id: this.product.id,
        productName: this.product.productName,
        productMainImage: '',
        productPrice: this.product.price,
        checked: 1,
        quantity: this.tempNum
      });
      if(this.tempNum == 1) {
        this.$refs.hcCart.orderAddCart(this.tempNum)
      }else {
        this.$refs.hcCart.orderUpdateCart(this.tempNum)
      }
    },
    subtractFromCart() {
      this.tempNum -= 1;
      this.$refs.hcCart.updataCart({
        id: this.product.id,
        productName: this.product.productName,
        productMainImage: '',
        productPrice: this.product.price,
        checked: 1,
        quantity: this.tempNum
      });
      this.$refs.hcCart.orderUpdateCart(this.tempNum)
    },
    // 交互操作结束
    // 数据操作
    getProductDetail() {
      getProductDetail().then((result) => {
        this.category = result.result.category;
        this.product = result.result.product;
        this.recommendPoint = result.result.recommendPoint || 10;
        this.store = result.result.store;
        this.storeId = result.result.storeId;
        this.tempNum = parseInt(result.result.curCartCount);
        this.userId = ''
        setTimeout(() => {
          this.$refs.hcCart.getCartVo()
        },0)
      })
    }
  },
  mounted() {
    this.getProductDetail()
  }
}
</script>

<style lang="scss">
.hc-goods {
  padding-top: 51px;
  padding-bottom: 60px;
  .hc-goods-header {
    position: fixed;
    width: 100%;
    left: 0px;
    top: 0px;
    z-index: 1000;
  }
  .mint-navbar {
    border-bottom: 1px solid #ebebeb;
    font-size: 17px;
    .mint-tab-item {
      margin-bottom: 0px !important;
      box-sizing: border-box;
      border-bottom: 4px solid transparent;
      &.is-selected {
        color: #f3ab03;
        border-bottom: 4px solid #f3ab03;
      }
      .mint-tab-item-label {
        // height: 100%;
        // display: inline-block;
      }
    }
  }
  .hc-goods-banner {
    width: 100%;
    height: px2rem(270);
    .hc-banner-item {
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .hc-goods-intr {
    background-color: #fff;
    padding: px2rem(15);
    .hc-intr-title {
      font-size: 16px;
      line-height: 30px;;
    }
    .hc-intr-intr {
      color: #b3b3b3;
    }
    .hc-intr-reco {
      line-height: 40px;
    }
    .hc-goods-cost {
      color: #b3b3b3;
      line-height: 40px;;
      .hc-cost-cost {
        color: #ff2d0b;
      }
      .hc-cost-cart {
        height: 30px;
        right: 0px;
        top: 0px;
        bottom: 0px;
        margin: auto;
        font-size: 30px;
        line-height: 0px;
        text-align: center;
        .hc-cart-icon {
          width: 30px;
          // background-color: #d7d7d7;
          height: 100%;
          display: inline-block;
          vertical-align: middle;
          line-height: 0px;
          &.hc-cart-num {
            line-height: 30px;
            width: auto !important;
          }
          .hc-subtract {
            color: #d7d7d7;
          }
          .hc-add {
            color: #fddb8a;
          }
        }
      }
    }
  }
  .hc-goods-shop {
    background-color: #fff;
    margin-top: 10px;
    .hc-shop-bar {
      height: 48px;
      padding: 0 px2rem(15);
      line-height: 48px;
      font-size: 16px;
      color: #3a3533;
      border-bottom: 1px solid #dbdbdb;
    }
    .hc-bar-icon {
      width: 8px;
      height: 14px;
      right: px2rem(15);
      top: 0px;
      bottom: 0px;
      margin: auto;
    }
    .hc-shop-msg {
      display: flex;
      align-items: center;
      padding: 15px;
      .hc-shop-img {
        width: 40px;
        height: 40px;
        display: inline-block;
        border-radius: 50%;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .hc-shop-intr {
        padding-left: 50px;
        line-height: 20px;
        .hc-f12 {
          color: #b3b3b3;
        }
      }
    }
  }
  .hc-goods-display {
    font-size: 0px;
    line-height: 0px;
    margin-top: 10px;
    img {
      width: 100%;
      height: auto;
    }
  }
  .hc-goods-params {
    background-color: #fff;
    margin-top: 10px;
    padding: 20px 0px;
    .hc-params-item {
      margin: 0px px2rem(15);
      line-height: 25px;
      border-bottom: 1px solid #e7e7e7;
      &:last-child {
        border: none;
      }
      div {
        display: inline-block;
      }
      .hc-item-left {
        width: 80px;
        text-align: right;
        color: #b3b3b3;
      }
      .hc-item-right {
        color: #3a3533;
      }

    }
  }
}
</style>



