<template>
  <div class="hc-cart-popup">
    <transition :name="currentTransition">
      <div v-show="currentValue" class="mint-popup hc-popup" :class="[position ? 'mint-popup-' + position : '']">
        <div class="hc-popup-box">
          <div class="hc-cart-header" id="hcPopupTitle">
            <div class="hc-cart-checkall">
              <hc-checkbox v-model="checkValAll" :disabled="checkValAllDisabled" @isCheckAll="isCheckAll" :isMain='true'/>
            </div>
            <div class="hc-fr hc-empty" @click="emptyCart">
              <svg class="icon hc-active" aria-hidden="true">
                <use xlink:href="#icon-lujing10"></use>
              </svg>
              清空购物车
            </div>
          </div>
          <div class="hc-popup-content">
            <div v-show="hcPopup.length>=1" v-for="(item, index) in hcPopup" :key="index" class="hc-popup-item hc-clearfix">
              <div>
                <div class="hc-cart-check hc-fl">
                  <hc-checkbox @isCheckAll="isCheckAll" :productId="item.productId" v-model="item.checked"/>
                </div>
                <div class="hc-cart-goods hc-pr">
                  <div class="hc-cart-goods-img">
                    <img :src="item.productMainImage" alt="">
                  </div>
                  <div class="hc-cart-intr">
                    <div class="hc-f14  hc-one-in">{{item.productName}}</div>
                    <div class="hc-c999">{{item.quantity}}</div>
                    <div class="hc-cart-cost"><span class="hc-f12">￥</span><span class="hc-f18 hc-cost">{{item.productPrice}}</span></div>
                  </div>
                  <!-- <div class="hc-cost-cart hc-pa">
                    <transition name="hc-roll">
                      <div v-show="item.quantity>=1" class="hc-cart-icon" @click="cartSubtract(item.id)">
                        <svg class="icon hc-subtract" aria-hidden="true">
                          <use xlink:href="#icon-ziyuan2"></use>
                        </svg>
                      </div>
                    </transition>
                    <div v-show="item.quantity>=1" class="hc-cart-icon hc-f16 hc-cart-num">{{item.quantity}}</div>
                    <div class="hc-cart-icon" @click="cartAdd(item.id)">
                      <svg class="icon hc-add" aria-hidden="true">
                        <use xlink:href="#icon-ziyuan3"></use>
                      </svg>
                    </div>
                  </div> -->
                  <hc-cart-btn
                    :tempNum="item.quantity"
                    @subtractFromCart="cartSubtract(item.id)"
                    @addToCart="cartAdd(item.id)"
                    class="hc-cost-cart"
                  ></hc-cart-btn>
                </div>
              </div>
            </div>
            <div v-show="hcPopup.length<=0" class="hc-cart-empty">
              <svg class="icon hc-active" aria-hidden="true">
                <use xlink:href="#icon-ziyuan"></use>
              </svg>
              <div class="hc-f14">您的购物车空空如也</div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="fade">
      <div class="hc-cart-bottom" v-show="totalNum>0||!needHide">
        <div class="hc-bottom-icon hc-pr" @click="showCart">
          <svg class="icon" :class="totalNum>=1?'hc-active':''" aria-hidden="true">
            <use xlink:href="#icon-lujing8"></use>
          </svg>
          <span v-show="totalNum>=1" class="hc-cart-num hc-pa">{{totalNum}}</span>
        </div>
        <div class="hc-cart-center" @click="showCart">
          <div v-show="totalNum>=1">
            <div><span class="hc-f12">￥</span><span class="hc-f18 hc-cost">{{totalPrice}}</span></div>
            <div class="hc-f12">共计{{totalNum}}件商品</div>
          </div>
          <div class="hc-f16" v-show="totalNum<=0">购物车</div>
        </div>
        <div class="hc-cart-btn" @click="showCart">立即下单</div>
      </div>
    </transition>
  </div>
</template>

<script type="text/babel">
  import { HcMessageBox } from '../../hcMessage/index.js'
  // import Popup from 'mint-ui/src/utils/popup';
  import { Popup } from 'mint-ui'
  import HcCheckbox from '../../hcCheckbox/hcCheckbox.vue'
  import HcCartBtn from '../../hcCartBtn/hcCartBtn.vue'
  // 获取购物车信息
  import { getCartVo, orderAddCart, orderUpdateCart, deleteCartProduct, selectOrUnSelect } from '../../../../api/axios.js'

  export default {
    name: 'hc-popup',

    mixins: [Popup],

    props: {
      modal: {
        default: true
      },

      // hcPopup: {
      //   type: Array,
      //   default: []
      // },
      needHide: {
        type: Boolean,
        default: false
      },

      hcTitle: {
        type: String,
        default: ''
      },

      modalFade: {
        default: true
      },

      lockScroll: {
        default: true
      },

      closeOnClickModal: {
        default: true
      },

      popupTransition: {
        type: String,
        default: 'popup-slide'
      },

      position: {
        type: String,
        default: 'bottom'
      },

      userId: {
        type: String,
        default: ''
      },

      storeId: {
        type: String,
        default: ''
      },

      productId: {
        type: String,
        default: ''
      }
    },

    components: {
      HcCheckbox,
      HcCartBtn
    },

    data() {
      return {
        currentValue: false,
        currentTransition: this.popupTransition,
        cartShow: true,
        tempIsCheckAll: false,
        checkValAllDisabled: false,
        hcPopup: []
      };
    },

    computed: {
      totalNum() {
        let num = 0;
        this.hcPopup.forEach((val) => {
          if(val.checked == 1) {
            num += val.quantity;
          }
        })
        return num;
      },
      totalPrice() {
        let price = 0;
        this.hcPopup.forEach((val) => {
          if(val.checked) {
            price += val.quantity * val.productPrice;
          }
        })
        return price;
      },
      checkValAll: {
        get: function() {
          let tempFlag = true;
          if(this.hcPopup.length <= 0) {
            this.checkValAllDisabled = true;
            return false;
          }else {
            this.checkValAllDisabled = false;
          }
          this.hcPopup.forEach((val) => {
            if(!val.checked) {
              tempFlag = false;
              this.temp = true
            }
          })
          return tempFlag;
        },
        set: function(newVal, oldVal) {
          if( this.tempIsCheckAll ) return;
          // this.$emit('checkAll', newVal);
          this.hcPopup.forEach((val) => {
              val.checked = newVal
          })
        }
      }
    },

    watch: {
      currentValue(val) {
        this.$emit('input', val);
      },

      value(val) {
        this.currentValue = val;
      },

      totalNum(val) {
        if(val == 0 && this.needHide) {
          this.currentValue = false;
        }
      }
    },

    beforeMount() {
      if (this.popupTransition !== 'popup-fade') {
        this.currentTransition = `popup-slide-${ this.position }`;
      }
    },

    methods: {
      showCart() {
        this.currentValue = !this.currentValue;
      },
      cartSubtract(id) {
        this.hcPopup.forEach((val, i) => {
          if(val.id == id) {
            val.quantity -= 1;
            this.orderUpdateCart(val.quantity);
            if(val.quantity == 0) {
              this.hcPopup.splice(i,1);
            }
          }
        })
      },
      cartAdd(id) {
        this.hcPopup.forEach((val, i) => {
          if(val.id == id) {
            val.quantity += 1;
            this.orderUpdateCart(val.quantity);
            if(val.quantity == 0) {
              this.hcPopup.splice(i,1);
            }
          }
        })
      },
      // 更新购物车操作
      updataCart(obj) {
        // this.orderAddCart();
        // this.hcPopup.forEach((val, index) => {
        //   if(val.id == obj.id) {
        //     this.hcPopup.splice(index, 1);
        //     return;
        //   }
        // })
        // if(obj.quantity > 0) {
        //   this.hcPopup.unshift(obj);
        // }
        // 重新获取购物车信息
        // this.getCartVo()
      },
      // 清空购物车
      emptyCart() {
        if(this.hcPopup.length <= 0) {
          HcMessageBox.confirm('您的购物车还没有商品哦~~')
        }else {
          HcMessageBox.alert('确定清空购物车？').then(() => {
            this.deleteCartProduct()
          })
        }
      },
      // 判断是否是全选
      isCheckAll(flag, productId, checked) {
        this.tempIsCheckAll = true;
        if(flag) {
          this.selectOrUnSelect(checked);
          this.tempIsCheckAll = false;
          return;
        };
        this.selectOrUnSelect(checked, productId);
        this.hcPopup.forEach((val) => {
          // 有一个不为true那就是没有全选
          if(!val.checked) this.tempIsCheckAll = false;
        });
      },
      getCartVo() {
        getCartVo({
          userId: this.userId,
          storeId: this.storeId
        }).then((result) => {
          this.hcPopup = result.result.cartProductVoList;
        })
      },
      // 新增数据到购物车
      orderAddCart(count=1) {
        orderAddCart({
          userId: this.userId,
          storeId: this.storeId,
          productId: this.productId,
          count: count
        }).then((result) => {
          this.getCartVo()
        })
      },
      // 更新数据到购物车
      orderUpdateCart(count=1) {
        orderUpdateCart({
          userId: this.userId,
          storeId: this.storeId,
          productId: this.productId,
          count: count
        }).then(() => {
          this.getCartVo()
        })
      },
      // 删除购物车
      deleteCartProduct() {
        let productIdStr = '';
        this.hcPopup.forEach((val) => {
          productIdStr += val.productId + ',';
        })
        productIdStr = productIdStr.substr(0,productIdStr.length-1)
        deleteCartProduct({
          userId: this.userId,
          storeId: this.storeId,
          productIds: productIdStr
        }).then(() => {
          this.hcPopup = [];
          HcMessageBox('成功清空购物车')
          this.showCart()
        })
      },
      // 选择
      selectOrUnSelect(checked, productId='') {
        selectOrUnSelect({
          userId: this.userId,
          storeId: this.storeId,
          productId: productId,
          checked: checked
        }).then(() =>{

        })
      }

    },

    mounted() {
      if (this.value) {
        this.rendered = true;
        this.currentValue = true;
        this.open();
      };

    }
  };
</script>

<style lang="scss" scoped>
  .fade2-enter-active,
  .fade2-leave-active {
      transition: all 0.3s;
  }
  .fade2-enter {
  }
  .fade2-enter-to {}

  .fade2-enter,
  .fade2-leave-to {
      transform: translateX(30px) rotate(360deg);
  }
  .hc-cost-cart {
    top: auto;
    right: 20px !important;
  }
  .hc-cart-popup {
    .hc-cart-add {
      display: inline-block;
      width: 20px;
      background-color: red;
      height: 20px;
    }
    .hc-bottom-icon::after {
        content: "";
        width: 1px;
        height: 40px;
        background-color: #ebebeb;
        display: inline-block;
        position: absolute;
        right: 0px;
        top: 0px;
        bottom: 0px;
        margin: auto;
    }
    .hc-bottom-icon {
      width: 60px;
      height: 100%;
      background-color: #fff;
      line-height: 55px;
      text-align: center;
      font-size: 22px;
      color: #b3b3b3;
      position: relative;
      .hc-active {
        color: #f3ab03;
      }
      .hc-cart-num {
        top: 10px;
        right: 10px;
        font-size: 12px;
        background-color: #ff2d0b;
        height: auto;
        width: auto;
        border-radius: 50%;
        line-height: 18px;
        min-width: 18px;
        color: #fff;
      }
    }
    .hc-cart-center {
      height: 100%;
      display: flex;
      align-items: center;
      padding-left: 10px;
      .hc-cost {
        color: #ff2d0b;
      }
    }
    .hc-cart-bottom {
      height: 55px;
      width: 100%;
      position: fixed;
      background-color: #fff;
      border-top: 1px solid #ebebeb;
      left: 0px;
      bottom: 0px;
      &>div {
        float: left;
      }
      .hc-cart-btn {
        width: 150px;
        line-height: 55px;
        float: right;
        text-align: center;
        background-color: #f3ab03;
        color: #fff;
        font-size: 17px;
      }
    }
    .hc-cart-header {
      text-align: left;
      height: 55px;
      padding: 0 px2rem(15);
      border-bottom: 1px solid #dbdbdb;
      .hc-cart-checkall {
        display: inline-block;
        padding: 20px 6px;
      }
      .hc-empty {
        line-height: 55px;
        font-size: 16px;
      }
    }
    .hc-popup-content {
      height: 384px;
      overflow-y: scroll;
      .hc-popup-item {
        // height: 130px;

        border-bottom: 1px solid #dbdbdb;
      }
      .hc-cart-check {
        width: 50px;
        line-height: 96px;;
      }
      .hc-cart-goods {
        display: flex;
        height: 81px;
        text-align: left;
        // align-items: center;
        padding-top: 15px;
        .hc-cart-goods-img {
          width: 60px;
          height: 60px;
        }
        img {
          width: 60px;
          height: 60px;
          display: inline-block;
        }
        .hc-cart-intr {
          padding-left: 10px;
          .hc-cart-cost {
            padding-top: 6px;
          }
          .hc-cost {
            color: #ff2d0b;
          }
        }
      }
      .hc-cart-empty {
        text-align: center;
        font-size: 90px;
        color: #bfbfbf;
        padding-top: 80px;
        line-height: 80px;;
      }
    }
  }
  .popup-slide-top-enter,
  .popup-slide-top-leave-active {
    transform: translate3d(-50%, -100%, 0);
  }

  .popup-slide-right-enter,
  .popup-slide-right-leave-active {
    transform: translate3d(100%, -50%, 0);
  }

  .popup-slide-bottom-enter,
  .popup-slide-bottom-leave-active {
    transform: translate3d(-50%, 100%, 0);
  }

  .popup-slide-left-enter,
  .popup-slide-left-leave-active {
    transform: translate3d(-100%, -50%, 0);
  }

  .popup-fade-enter,
  .popup-fade-leave-active {
    opacity: 0;
  }
  .hc-popup {
    width: 100%;
    text-align: center;
    height: auto;
    margin-bottom: 55px;
  }
</style>



