<template>
  <div class="hc-com-checkbox">
    <label for="label" @change="clickMe">
        <input class="hc-checkbox" :disabled="disabled" type="checkbox" id="label" v-model="checkVal" >
    </label>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      // type: Boolean
    },
    isMain: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    productId: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      checkVal: this.value
    }
  },
  computed: {

  },
  watch: {
    value(val) {
      this.checkVal = val
    },
    checkVal(val) {
      this.$emit('input', val)
    }
  },
  methods: {
    clickMe(){
      let checked = 0;
      if(this.checkVal) checked = 1;
      this.$emit('isCheckAll', this.isMain, this.productId, checked)
    }
  }
}
</script>

<style lang="scss">
.hc-com-checkbox {
  .hc-checkbox::before {
    content: "";
    width: 20px;
    height: 20px;
    background-color: #ffffff;
    border: 1px solid #d6d6d6;
    display: inline-block;
    position: absolute;
    left: -10px;
    right: 0px;
    top: -10px;
    bottom: 0px;
    margin: auto;
    border-radius: 50%;
  }
  .hc-checkbox::after {
      border: 2px solid transparent;
      border-left: 0;
      border-top: 0;
      content: "";
      left: -2px;
      right: 0px;
      top: -13px;
      bottom: 0px;
      margin: auto;
      position: absolute;
      width: 5px;
      height: 12px;
      -webkit-transform: rotate(45deg) scale(0);
      transform: rotate(45deg) scale(0);
      -webkit-transition: -webkit-transform .2s;
      transition: -webkit-transform .2s;
      transition: transform .2s;
      transition: transform .2s,-webkit-transform .2s;
  }
  .hc-checkbox:checked::before {
      /* background-color: #000; */
      background-color: #f17006;
      border: 1px solid #f17006;
  }
  .hc-checkbox:checked::after {
      border-color: #fff;
      -webkit-transform: rotate(45deg) scale(1);
      transform: rotate(45deg) scale(1);
  }
  .hc-checkbox[disabled]::before {
    background-color: #d9d9d9;
    border: 1px solid #d9d9d9;
  }
  .hc-checkbox {
      /* display: none; */
      position: relative;
      width: 0px;
      height: 0px;
  }

}


</style>



