 /**
* 组件名称
* @desc 前端购物车组件封装
* @author 林雄军
* @date 2018年08月29日
* @example 调用示例
* 1，引入组件
* import hcShopCart from '../../components/common/hcShopCart'
* 2，<hc-shop-cart
      v-model="popupRoleVisible"            Boolean 展示和隐藏购物车列表
      ref="hcCart"                          String 组件名称
      :userId='userId'                      String 用户id
      :storeId='storeId'                    String 门店Id
      :productId='product.spec.productId'   String 商品Id
    ></hc-shop-cart>
* 组件内的方法：
    showCart()                              隐藏和显示购物车
    cartSubtract(id)                        根据id减购
    cartAdd(id)                             根据id加购
    updataCart(obj)                         更新购物车，传入的是新加的商品，可不传
    emptyCart()                             清空购物车
    isCheckAll(flag, productId, checked)    判断是否全选
    getCartVo()                             获取购物车列表信息
    orderAddCart()                          从父组件增加数据到购物车列表
    orderUpdateCart()                       从父组件更新数据到购物车列表
    deleteCartProduct()                     删除购物车
    selectOrUnSelect()                      选择和取消
*
*/
export { default } from './src/hcCartPopup.vue';



