<template>
  <!-- <div class="hc-cart-btn"> -->
    <div class="hc-cost-cart hc-pa">
      <transition name="hc-roll">
        <div v-show="tempNum>=1" class="hc-cart-icon" @click="subtractFromCart">
          <svg class="icon hc-subtract" aria-hidden="true">
            <use xlink:href="#icon-ziyuan2"></use>
          </svg>
        </div>
      </transition>
      <div v-show="tempNum>=1" class="hc-cart-icon hc-f16 hc-cart-num">{{tempNum}}</div>
      <div class="hc-cart-icon" @click="addToCart">
        <svg class="icon hc-add" aria-hidden="true">
          <use xlink:href="#icon-ziyuan3"></use>
        </svg>
      </div>
    </div>
  <!-- </div> -->
</template>

<script>
export default {
  props: {
    tempNum: {
      type: Number,
      default: 0
    }
  },
  methods: {
    subtractFromCart() {
      this.$emit('subtractFromCart')
    },
    addToCart() {
      this.$emit('addToCart')
    }
  }
}
</script>

<style lang="scss">
// .hc-cart-btn {
  .hc-cost-cart {
    height: 30px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    margin: auto;
    font-size: 30px;
    line-height: 0px;
    text-align: center;
    .hc-cart-icon {
      width: 30px;
      // background-color: #d7d7d7;
      height: 100%;
      display: inline-block;
      vertical-align: middle;
      line-height: 0px;
      &.hc-cart-num {
        line-height: 30px;
        width: auto !important;
      }
      .hc-subtract {
        color: #d7d7d7;
      }
      .hc-add {
        color: #fddb8a;
      }
    }
  }
// }
</style>



